ul {list-style-type: none;}
body {font-family: Verdana, sans-serif;}

/* Month header */
.month {
  /* padding: 70px 25px; */
  width: 100%;
  background: #1abc9c;
  text-align: center;
}

/* Month list */
.month ul {
  margin: 0;
  padding: 0;
}

.month ul li {
  color: white;
  font-size: 20px;
  text-transform: uppercase;
  letter-spacing: 3px;
}

/* Previous button inside month header */
.month .prev {
  float: left;
  padding-top: 10px;
}

/* Next button */
.month .next {
  float: right;
  padding-top: 10px;
}

/* Weekdays (Mon-Sun) */
.weekdays {
  margin: 0;
  padding: 10px 0;
  background-color:#ddd;
}

.weekdays thead tr td {
  width: 13.6%;
  color: #666;
  text-align: center;
}

/* Days (1-31) */
.days {
  padding: 10px 0;
  background: #eee;
  margin: 0;
}

.days tr td {
  list-style-type: none;
  width: 13.6%;
  text-align: center;
  margin-bottom: 5px;
  font-size:12px;
  color: #777;
  padding:10px;
}

/* Highlight the "current" day */
.days tr td .active {
  padding: 5px;
  background: #1abc9c;
  color: white !important
}

.launch {
  background-color: #7c40b7 !important;
  color: white !important;
}

.dinner {
  background-color: #ffb043 !important;
  color: white !important;
}

.both {
  background-color: #4fbf40 !important;
  color: white !important;
}

.title {
